<template>
  <div class="issue">
    <br>
    <h3>付刚的笔记</h3>
    <br>
    <div class="card border-success" @click="link('/issues/1')">
      <div class="card-header">笔记</div>
      <div class="card-body">
        <h4 class="card-title">我的第一个笔记</h4>
        <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  methods:{
    link:function(to){
      this.$router.push(to)
    }
  }
}
</script>


